.BuildControl {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px 0;
  & button {
    display: block;
    font: inherit;
    padding: 5px;
    margin: 0 5px;
    width: 80px;
    border: 1px solid #aa6817;
    cursor: pointer;
    outline: none;
    border-radius: 5px;
  }
  & .Less {
    background-color: #d39952;
    color: white;
  }
  & .More {
    background-color: #8f5e1e;
    color: white;
  }
}
.Label {
  padding: 10px;
  font-weight: bold;
  width: 100px;
  text-align: right;
}

.BuildControl button.Less:disabled {
  background-color: #ac9980;
  color: #ccc;
  cursor: not-allowed;
}
.BuildControl button.More:disabled {
  background-color: #ac9980;
  color: #ccc;
  cursor: not-allowed;
}
