.Modal {
  position: fixed;
  z-index: 500;
  background-color: white;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 16px;
  width: 60%;
  left: 20%;
  top: 30%;
  transition: all 0.3s ease-out;
}
@media (min-width: 600px) {
  .Modal {
    width: 500px;
    left: calc(50%-250px);
  }
}
